.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.right{
  text-align: right;
  align-items: right;
  justify-content: right;
  padding-top: 5px;
  padding-right: 5px;
}

#main{
  border: 3px solid#4FB4B1;
  padding: 2px;
}
.App-header {
 
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html{
  background-color: #33766F;

}